const { registerBlockType } = wp.blocks;
const { RichText, InspectorControls, ColorPalette } = wp.blockEditor;
const { PanelBody } = wp.components;


//Logo para el bloque
import { ReactComponent as Logo } from '../manzana.svg';


/*
    Siete pasos para crear un bloque en gutenberg
    1. Importar lo(s) componente(s) que utilizarás.
    2. Coloca el componente donde deseas utilizarlo.
    3. Crea una funcion que lea los contenidos.
    4. Registra un atributo.
    5. Extraer el contenido desde props.
    6. Guarda el contenido con setAttributes.
    7. Lee los contenidos guardados en save().
*/
registerBlockType('manzanita/boxes', {
    title: 'Boxes',
    icon: {src: Logo},
    category: 'manzanita-gutenberg',
    attributes: {
        headingBox : {
            type: 'string',
            source: 'html',
            selector: '.box h2'
        },
        textBox : {
            type: 'string',
            source: 'html',
            selector: '.box p'
        },
        bgColor : {
            type: 'string'
        },
        textColor : {
            type: 'string'
        }
    },

    edit: (props) => {
            const { attributes: { headingBox, textBox, bgColor, textColor }, setAttributes } = props;

            const onChangeHeadingBox = (headingBox) => {
                setAttributes({ headingBox });
            }
            
            const onChangeTextBox = (textBox) => {
                setAttributes({ textBox });
            }
            
            const onBgColor = (bgColor) =>{
                setAttributes({ bgColor });
            }

            const onTextColor = (textColor) => {
                setAttributes({ textColor });
            }        

            return(
            <>    
                    <InspectorControls>
                        <PanelBody
                        title={'Ajustes de color'}
                        initialOpen={true}>
                            <div className="components-base-control">
                                <div className="components-base-control__field">
                                    <label className="components-base-control__label">Color de Fondo</label>
                                </div>
                                <ColorPalette 
                                    value={ bgColor }
                                    onChange = {onBgColor}
                                />
                                <div className="components-base-control__field">
                                    <label className="components-base-control__label">Color del texto</label>
                                </div>
                                <ColorPalette
                                    value = { textColor }
                                    onChange = {onTextColor}
                                />
                            </div>
                        </PanelBody>
                    </InspectorControls>
                <div className='box' style={{backgroundColor: bgColor}}>
                    <h2 style={{color:textColor}}>
                        <RichText
                            placeholder="Agrega el encabezado aquí"
                            onChange={onChangeHeadingBox}
                            value={headingBox}
                        />
                    </h2>
                    <p style={{color:textColor}}>
                        <RichText 
                            placeholder="Agrega el texto aquí"
                            onChange={onChangeTextBox}
                            value= {textBox}    
                        />
                    </p>
                </div>
            </>
            )
        },
    save: (props) => {
        const { attributes: { headingBox, textBox, bgColor, textColor } } = props;

        return(
            <div className='box' style={{backgroundColor: bgColor}}>
                <h2 style={{color: textColor}}>
                    <RichText.Content value={headingBox} />
                </h2>
                <p style={{color: textColor}}>
                    <RichText.Content value={textBox} />
                </p>
            </div>
        )
    }
    
});



